.Language {
    -ms-flex: 1 1;
        flex: 1 1;
    color: white;
    cursor: pointer;
}

.Language.En {
    color: #E8AE40;
}

.Language.Fr {
    color: #E8AE40;
}

.Language li {
    padding-left: 5px;
}

.Language ul {
    list-style: none;
    margin: 0;
    padding: 0;
    list-style: none;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    height: 100%;
}

@media(max-width: 850px) {
    .Language {
        color: black;
    }
    .Language li {
        padding-left: 20px;
    }
}


