.Header {
    position: fixed;
    width: 100%;
    background: #5B5D62;
    height: 90px;
    z-index:900;
    top:0;
    left:0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding: 0 1rem;
    -ms-flex-align: center;
        align-items: center;
  }

.NavWrapper {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: end;
      align-items: flex-end;
  line-height: 2rem;
}

@media (max-width: 850px) {
  .NavWrapper {
      display: none;
  }
  
  .Header {
    height: 56px;
  }
}


