.Backdrop {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    background: rgba(0,0,0,0.5);
}

@media(min-width: 851px) {
    .Backdrop {
        display: none;
    }
}


